html {
  --header-text-color: #282c34;
  --main-bg: white;
  --rule-color: #dddddd;
  --hit-bg: #6aaa64;
  --close-bg: #c9b458;
  --miss-bg: #8f96aa; /* #888888 */
  --trust-symbol-bg: #a8abb9; /* #aaaaaa; */
  --blank-square-text-color: #282c34; /* black; */
  --blank-unoccupied-border: #dddddd;
  --blank-occupied-border: #a8abb9; /* #aaaaaa; */
  --past-square-text-color: white;
  --satisfaction-color: #282c34; /* black; */
  --give-up-bg: black;
  --message-bg: black;
  --message-text-color: white;
  --win-bg: var(--hit-bg);
  --keyboard-blank-text-color: black;
  --keyboard-blank-bg: #dbe0f5; /* #d3d3d3; */
  --keyboard-off-text-color: #a8abb9;
  --keyboard-off-bg: #eceffc;
  --link-color: #abb3c7;
}

.high-contrast {
  --hit-bg: #f5793a;
  --close-bg: #85c0f9;
}

.dark-mode {
  --header-text-color: #DDDDDD;
  --main-bg: #333333;
  --rule-color: #888888;
  --miss-bg: #686e7c; /* #888888 */
  --trust-symbol-bg: #5e626b;
  --blank-square-text-color: white;
  --blank-unoccupied-border: #666666;
  --blank-occupied-border: #999999;
  --past-square-text-color: white;
  --satisfaction-color: black;
  --give-up-bg: black;
  --message-bg: black;
  --message-text-color: white;
  --win-bg: var(--hit-bg);
  --keyboard-blank-text-color: black;
  --keyboard-blank-bg: #abb3c7; /* #d3d3d3; */
  --keyboard-off-text-color: #7f8491;
  --keyboard-off-bg: #5f636e;
  --link-color: #dbe0f5;
}

.App {
  text-align: center;
  color: var(--header-text-color);
}

.App-header {
  background-color: var(--main-bg);
  /* transition: background-color 10s; */
  min-height: 100vh;
  display: flex;
  flex-direction: row;
  justify-content: center;
  font-size: calc(10px + 2vmin);
}

/* .App-header.hide {
  display: none;
} */

.main-column {
  min-height: 100vh;
  width: 100vw;
  max-width: 500px;
  display: flex;
  flex-direction: column;
  align-items: stretch;
  justify-content: flex-start;
}

.game-header, .modal-header {
  display: flex;
  flex-direction: row;
  justify-content: stretch;
  align-items: center;
  flex: 0 0 50px;
  z-index: 1;
}

.game-header {
  border-bottom: 1px solid var(--rule-color);
  position: sticky;
  background-color: var(--main-bg);
  top:0;
}

.modal-header {
  margin-bottom: 12px;
}

.game-header-title {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  flex: 4;
  font-size: 28pt;
  /* font-weight: bold; */
}

.game-header-title sup {
  font-size: 18pt;
}

.modal-title {
  display: flex;
  flex-direction: row;
  /* justify-content: flex-start; */
  justify-content: center;
  align-items: center;
  flex: 4;
  font-size: 16pt;
  font-weight: bold;
}

.game-header-button, .modal-close-button {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  align-self: stretch;
  flex: 0 50px;
  overflow: hidden;
  background: none;
  border: 0;
  font-size: 18pt;
  font-weight: bold;
  color: var(--header-text-color);
}

.game-header svg {
  fill: var(--header-text-color);
}

/* .modal-close-button {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  flex: 0;
  font-size: 18pt;
  font-weight: bold;
} */

.main-area{
  display: flex;
  flex-direction: column;
  align-items: center;
  flex: 1;
}

.gameboard, .gameboard-explainer {
  display: flex;
  flex-direction: column;
  grid-gap: 5px;
  width: 100%;
  max-width: 400px;
  margin: 8px 0 2px 0;
}

.gameboard-explainer {
  margin: 8px 0;
  align-self: center;
}

.gameboard-row {
  display: flex;
  flex-direction: row;
  justify-content: center;
  grid-gap: 5px;
}

.gameboard-row.distrust {
  opacity: 45%;
}

.gameboard-square {
  aspect-ratio: 1 / 1;
  max-height: 10vh;
  max-width: 10vh;
  display: flex;
  flex: 1;
  flex-direction: column;
  align-items: stretch;
  justify-content: stretch;
  font-size: 24pt;
  font-weight: bold;
  padding: 0;
  overflow: hidden;
}

.gameboard-square-inner {
  display: flex;
  flex: 1;
  margin: 0;
  justify-content: center;
  align-items: center;
}

.gameboard-square.hit {
  color: var(--past-square-text-color);
  background-color: var(--hit-bg);
}

.gameboard-square.close {
  color: var(--past-square-text-color);
  background-color: var(--close-bg);
}

.gameboard-square.miss {
  color: var(--past-square-text-color);
  background-color: var(--miss-bg);
}

.gameboard-square.blank {
  color: var(--blank-square-text-color);
}

.gameboard-square.blank .gameboard-square-inner{
  border: 3px solid var(--blank-unoccupied-border);
}

.gameboard-square.blank.occupied .gameboard-square-inner {
  border-color: var(--blank-occupied-border);
}

.gameboard-square.unsatisfied .gameboard-square-inner {
  /* margin: 2px; */
  /* border: 6px solid black; */
  color: var(--satisfaction-color);
  /* border-radius: 50%; */
}

.gameboard-row.given-up .gameboard-square {
  border: 0;
  background-color: var(--give-up-bg);
  color: var(--past-square-text-color);
}

.gameboard-row.given-up .gameboard-square .gameboard-square-inner {
  border: 0;
}

.trust-button {
  aspect-ratio: 1 / 2;
  max-height: 10vh;
  max-width: 5vh;
  display: flex;
  flex-direction: column;
  flex: 0.5;
  justify-content: center;
  align-items: center;
  background: none;
  color: var(--trust-symbol-bg);
  border: 0;
  font-size: 24px;
  font-weight: bold;
  padding: 0;
  cursor: pointer;
  overflow: hidden;
}

.trust-button.arrow {
  color: var(--header-text-color);
  font-size: 30px;
}

.trust-button.arrow svg {
  fill: var(--header-text-color);
}

.trust-button .margin-small {
  font-size: 12px;
  margin-bottom: -2px;
  line-height: 1;
}

.message {
  display: flex;
  align-self: center;
  /* flex: 0.5; */
  min-height: 1.5em;
  justify-content: center;
  align-items: center;
  background-color: var(--message-bg);
  color: var(--message-text-color);
  border-radius: 4px;
  font-size: 12pt;
  padding: 4px 12px;
}

.message.max-lies-add-message {
  margin-top: 10px;
}

.message.win {
  background-color: var(--win-bg);
}

.keyboard {
  position: sticky;
  bottom: 0;
  display: flex;
  flex-direction: column;
  max-height: 40vh;
  flex: 0 190px;
  justify-content: stretch;
  align-items: stretch;
  gap: 8px;
  padding: 8px 0 12px 0;
  background-color: var(--main-bg);
}

.keyboard > * {
  flex: 1;
  /* height: 58px; */
}

.keyboard-row {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: stretch;
  gap: 4px;
}

.keyboard-button, .trust-symbol {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: 12pt;
  font-weight: bold;
  color: var(--keyboard-blank-text-color);
  border-radius: 4px;
  border: 0;
  padding: 0;
  overflow: hidden;
}


.keyboard-button.letter {
  flex: 0 1 calc(10% - 4px);
}

.keyboard-button svg {
  fill: var(--keyboard-blank-text-color);
}

.trust-symbol.confident {
  aspect-ratio: 2 / 3;
  height: 55%;
  background-color: var(--trust-symbol-bg);
  color: var(--past-square-text-color);
}
.trust-symbol.unsure {
  aspect-ratio: 1;
  height: 35%;
  border-radius: 50%;
  background-color: var(--trust-symbol-bg);
  color: var(--past-square-text-color);
}
.trust-symbol.distrust {
  aspect-ratio: 2 / 3;
  height: 50%;
  color: var(--trust-symbol-bg);
}

.keyboard-button.hit, .keyboard-button-inner.hit {
  background-color: var(--hit-bg);
  color: var(--past-square-text-color);
}
.keyboard-button.close, .keyboard-button-inner.close {
  background-color: var(--close-bg);
  color: var(--past-square-text-color);
}
.keyboard-button.miss, .keyboard-button-inner.miss {
  background-color: var(--miss-bg);
  color: var(--past-square-text-color);
}
.keyboard-button.blank, .keyboard-button-inner.blank {
  background-color: var(--keyboard-blank-bg);
  color: var(--keyboard-blank-text-color);
}

.keyboard-button-inner {
  display: flex;
  width: 29px;
  aspect-ratio: 1;
  /* flex: 1; */
  /* margin: 5px; */
  /* align-self: center; */
  padding: 0 0 0 0;
  /* margin: 14px 0 14px 0; */
  justify-content: center;
  align-items: center;
  /* background-color: #dddddd; */
  /* border-radius: 4px 4px 0 0; */
  border-radius: 50%;
}

.keyboard-button.wide {
  flex: 0 1 calc(15% - 6px);
  background-color: var(--keyboard-blank-bg);
  justify-content: center;
  align-items: center;
  font-size: 10pt;
}

.keyboard-explainer-row {
  display: flex;
  flex-direction: row;
  justify-content: stretch;
  align-items: center;
  margin: 5px 0;
  /* background-color: green; */
}

.keyboard-explainer-letter-wrapper {
  display: flex;
  flex-direction: row;
  justify-content: stretch;
  align-items: stretch;
  flex: 0;
  padding: 0 20px;
}

.keyboard-explainer-letter-wrapper .keyboard-button {
  flex: 0 1 100%;
  width: 38px;
  height: 58px;
}

.keyboard-explainer-text {
  flex: 1;
  text-align: left;
}

.bottomElement {
  display: flex;
  flex: 1;
}

.modal.hide {
  display: none;
}

.modal.show {
  position: fixed;
  overflow: auto;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  left: 0;
  top: 0;
  height: 100vh;
  width: 100vw;
  /* background-color: rgba(0, 0, 0, 0.5); */
  background-color: var(--main-bg);
  z-index: 2;
}

.modal-content {
  padding: 0 8px;
  display: flex;
  flex-direction: column;
  align-items: stretch;
  flex: 1;
  /* font-size: 14pt; */
  /* grid-gap: 20px; */
}

.modal-content p {
  text-align: left;
  margin: 0.5em 0;
  padding: 0 5px;
}

.settings-row {
  display: flex;
  flex-direction: row;
  justify-content: stretch;
  align-items: center;
  border-bottom: 1px solid var(--rule-color);
  padding: 10px 0px;
}

.spacer {
  flex: 1;
}

.version-row {
  flex: 0;
  justify-self: flex-end;
  display: flex;
  flex-direction: row;
  justify-content: stretch;
  align-items: center;
  padding: 10px 0px;
}

.version-row .left {
  flex: 1;
  text-align: left;
}

.version-row .right {
  flex: 1;
  text-align: right;
}

/* .settings-row {
  display: flex;
  flex-direction: row;
  justify-content: stretch;
  align-items: center;
  border-bottom: 1px solid var(--rule-color);
  padding: 10px 0px;
} */

.settings-row.continues {
  border-bottom: none;
  /* padding-bottom: 0; */
}

.settings-row.continued {
  border: 1px solid var(--rule-color);
  border-radius: 8px;
  padding: 10px;
  margin: 0 20px;
}

.game-over-row {
  display: flex;
  flex-direction: row;
  justify-content: stretch;
  align-items: center;
  padding: 10px 0px;
}

.big-button {
  flex: 1;
  margin: 0 5%;
  height: 36px;
  /* border: 1px solid #dddddd; */
  border: 0;
  background-color: var(--keyboard-blank-bg);
  border-radius: 8px;
  font-size: 14px;
  /* font-weight: bold; */
}

.big-button.share-success {
  color: white;
  background-color: var(--hit-bg);
  font-weight: bold;
}

.big-button.share-error {
  color: var(--past-square-text-color);
  background-color: var(--give-up-bg);
}

@media screen and (max-width: 350px) {
  .keyboard-button.wide {
    font-size: 8pt;
  }
}

.settings-row label {
  display: flex;
  flex-direction: row;
  justify-content: stretch;
  align-items: center;
  flex: 1;
  gap: 20px;
}

.settings-row .setting-label {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  flex: 1;
  text-align: left;
}

.setting-name {
  font-size: 16px;
  font-weight: bold;
}

.setting-description {
  /* display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: flex-start; */
  font-size: 12px;
  color: #888888;
}

.settings-row .switch {
  display: flex;
  flex: 0;
}

.settings-row a {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  flex: 0;
}

a {
  color: var(--link-color);
}

.number-input {
  display: flex;
  flex-direction: row;
  justify-content: stretch;
  align-items: stretch;
  flex: 0 100px;
  height: 30px;
}

.number-input button {
  display: flex;
  justify-content: center;
  align-items: center;
  flex: 1 30px;
  /* font-weight: bold; */
  background-color: var(--keyboard-blank-bg);
  color: var(--keyboard-blank-text-color);
  border: 0;
  border-radius: 4px;
  font-size: 24px;
  padding: 0;
}

.number-input button.off {
  /* visibility: hidden; */
  background-color: var(--keyboard-off-bg);
  color: var(--keyboard-off-text-color);
}

.number-input .number-input-value {
  display: flex;
  justify-content: center;
  align-items: center;
  flex: 1 40px;
}